






















import { Vue, Component, Prop } from 'vue-property-decorator';
import crumbs from '../components/crumbs.vue';

@Component({
  components: { crumbs },
})
export default class TestMore extends Vue {
  @Prop() type!: string;
  @Prop() id!: number;

  mounted() {
    this.Id = this.$route.query.id as any;
    this.TYPE = this.$route.query.type as any;

    let name = '工作计划';
    switch (+this.TYPE) {
      case 0:
        name = '工作计划';
        break;
      case 1:
        name = '例会纪要';
        break;
      case 2:
        name = '工艺纪律检查';
        break;
    }
    this.path = ['网站首页', name];
    this.name = name;
    this.getList();
  }

  Id = '';
  TYPE = '';
  name = '工作计划';
  path = ['网站首页', '例行试验'];
  getList() {
    console.log('调用接口', this.TYPE, this.Id);
  }
}
